<template>
	<div class="container">
		<div class="row">
			<div class="col-md-2"></div>
			<div class="col-md-8">
				<input type="text" class="form-control" placeholder="请输入搜索关键字" v-model="search"  >
				<div class="panel bg-primary" v-for="item in searchBlog" v-rainbow:bg>
					<router-link :to="'/blog/'+item.id"><h2 v-rainbow:color>{{item.title | upperCase}}</h2></router-link>
					<router-link :to="'/blog/'+item.id"><p v-rainbow:color>{{item.body | capitalize}} </p></router-link>
				</div>
			</div>
			<div class="col-md-2"></div>
		</div>
	</div>
</template>
<script>
	//import axios from 'axios'  每个组件都需引入方可使用 axios.get()
	export default{
		name:'showBlog',
		data(){
			return {
				search:'',
				content:[]
			}
		},
		created:function(){
			this.$http.get("/api/posts")
				.then((data) => {
					this.content = data.data.slice(0,20); //截取原数组1-20返回新的子数组；
				})
				 
			/* var _this = this;
			axios.get("http://jsonplaceholder.typicode.com/posts")
				.then(function(data){    非箭头函数内 this 指当前function
					console.log(data);
					_this.content = data.data.slice(0,20) ; //截取原数组1-20返回新的子数组；
				})*/
		},
		computed:{
			searchBlog:function(){
				return  this.content.filter((item) =>{
					return item.title.match(this.search);
				})
			}
		},
		directives:{
			rainbow:{
				bind:function(el,binding,vnode){
					if(binding.arg =="color"){
						el.style.color = "#"+Math.random().toString().slice(3,9);
					}else if(binding.arg == "bg"){
						el.style.backgroundColor = "#"+Math.random().toString().slice(3,9);
					}
				}
			},
			alpha:{
				bind(el,binding,vnode){
					// 。。。。
				}
			}

		},
		filters:{
			capitalize:function(value){
				if(!value) return '';
				value = value.toString();
				return value.charAt(0).toUpperCase() + value.slice(1);
			} 

		}

	}
</script>



