<template>
<div class="container">
	<div class="row"> 
		<div class="col-md-2"></div>
		<div class="col-md-8">
			<div class="panel bg-primary">
				<h2>{{title}}</h2>
				<p>{{body}}</p>	  
			</div>
		</div>
		<div class="col-md-2"></div>	 
	</div>
</div>	 
</template>
<script> 
	//import axios from 'axios'  每个组件都需引入方可使用 axios.get()
	export default {
		name:'singleBlog',
		data(){
			return{
				title:'',
				body:''
			}
		},
		created:function(){
			console.log("this.$router.params-------------");
			console.log(this.$route.params);
			this.$http.get("/api/posts/"+this.$route.params.id)
				.then((response) => { 
					console.log(response);
					this.title = response.data.title;
					this.body = response.data.body;
				})
			/*axios.get("http://jsonplaceholder.typicode.com/posts/"+this.$route.params.id)
				.then((response) => { 
					console.log(response);
					this.title = response.data.title;
					this.body = response.data.body;
				})*/
		}
 
	}
</script>


