import Vue from 'vue'
import Router from 'vue-router'
import addBlog from '@/components/addBlog'
import showBlog from '@/components/showBlog'
import singleBlog from '@/components/singleBlog'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'showBlog',
      component: showBlog
    },
    {
      path: '/add',
      name: 'addBlog',
      component: addBlog
    },
    {
      path: '/blog/:id',
      name: 'singleBlog',
      component: singleBlog
    }
  ],
  mode:'history'
})



