<template>
	<div class="container">
		<div class="row">
			<div class="col-md-2"></div>
			<div class="col-md-8">
		  		<form class="form">
		  			<div class="form-group">
		  				<label>主题：</label>
		  				<input type="text" class="form-control" v-model="blog.title">
		  			</div>
		  			<div class="form-group">
		  				<label>内容：</label>
		  				<textarea rows="5" class="form-control" v-model="blog.body"></textarea>
		  			</div>
		  			<div class="form-group">
		  				<label>类别：</label>
		  				<input type="checkbox" value='vue.js' v-model="category">   Vue.js
		  				<input type="checkbox" value='node.js' v-model="category">   Node.js
		  				<input type="checkbox" value='anglar.js' v-model="category">   Anglar.js
		  				<input type="checkbox" value='reat.js' v-model="category">   Reat.js
		  			</div>
		  			<div class="form-group">
		  				<label>作者：</label>
		  				<select v-model="author">
		  					<option value="author" v-for="author in whoAdd">{{author}}</option>
		  				</select>
		  			</div>
		  			<div class="btn btn-success" v-on:click="addBlog">发布</div>
		  			<p>{{blog.title}}</p>
		  			<p>{{blog.body}}</p>
		  			<p>{{category}}</p>
		  			<p>{{author}}</p>
		  		</form>
		 	</div>
			<div class="col-md-2"></div>
		</div>
	</div>
	
 	
</template>
<script>
//import axios from 'axios'  每个组件都需引入方可使用 axios.get()
	export default {
		name:'addBlog',
		data(){
			return{
				blog:{
					title:'',
					body:'',
					id:1,
					userId:1
				},
				author:'',
				category:[],
				whoAdd:['Jack',"Hunter","Spencer"]
			}
		},
		methods:{
			addBlog(){
				this.$http.post("/api/posts",this.blog)
					.then(function(data){
						console.log(data);
					})
				/*axios.post("http://jsonplaceholder.typicode.com/posts",this.blog)
					.then(function(data){
						console.log(data);
					})*/
			}
		}
	}
</script>


